@use "./vars";
// LCARS (pronounced elkarz) is an acronym for Library Computer Access/Retrieval System, and was the fictional computer operating system on many Star Trek incarnations.right-frame-top {

.wrap {
	padding: 0 0px 0 20px;
}

.button a {
	font-size: 1rem;
}

.button-col {
	border-left: 15px solid transparent;
}
.bar-1,
.bar-2,
.bar-3,
.bar-4,
.bar-6,
.bar-7,
.bar-8,
.bar-9 {
	border-right: 5px solid transparent;
	margin-right: 5px;
}
// .hop {
// font-size: 1vw;
// }

// colour overrides
.panel-4 {
	// background-color: vars.$theme-colour4;
	padding: 40px 10px 15px 0;
}

.right-frame-top,
.top-corner-bg,
.top-corner {
	// flex: 1 1;
	background-color: #1c2a7cbe;
	// filter: blur(10px);
}

.lcars-text-bar {
	span {
		position: absolute;
		top: 0px;
		background-color: #000;
		height: 32px;
		overflow: visible;
		padding: 0 10px;
		font-size: 1.5rem;
		line-height: 27px;
		text-transform: uppercase;
		color: #fa0;
	}
}
@media only screen and (max-width: 600px) {
	.lcars-text-bar {
		span {
			position: absolute;
			top: 0px;
			height: 24px;
			padding: 0 5px;
			font-size: 1.23rem;
			line-height: 20px;
		}
	}
}
.top-corner {
	-webkit-mask-image: url(#mask);
	mask-image: url(#mask);
}
.left-frame,
.left-frame-top {
	// width: 10vw;
	// min-width: 10vw;
}

.right-frame,
.content {
	background-color: transparent;
	user-select: none;
	pointer-events: none;
	height: 100%;
	padding-right: 5px;
}
.right-frame-top {
	.banner {
		padding-right:12px;
		.source {
			color: vars.$theme-colour1;
		}
	}
	@media only screen and (max-width: 500px) {
		.banner {
			padding: 10px 15px 10px 3px;
		}
	}
}

.lcars-header-container {
	// height: 14vh;
}
.lcars-body-container {
	// height: 82vh;
	overflow: hidden;
}
.data-cascade-button-group {
	padding: 5px;
}
.bar-panel {
	// min-height: 22px;
}

@media only screen and (max-width: 500px) {
	.panel-2 {
		padding-bottom: 0px;
		border-bottom: 5px solid transparent;
		background-color: #25f;
	}
}
